.vaiLoginBody {
  height: 100vh;
  background-color: #0c212b;
  width: 100vw;
  position: relative;
  font-family: 'SF';
}

.vaiLoginPanel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 530px;
  width: 520px;
  background-color: #fafafa;
  border-radius: 10px;
}

.vaiLoginLogo {
  height: 60px;
  width: auto;
}

.vaiTitleText {
  color: #6d6d73;
  margin-top: 40px;
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
}

.vaiDescription {
  font-weight: 400;
  font-size: 15px;
  line-height: 17.9px;
  letter-spacing: 3%;
  width: 390px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 40px;
}

.vaiLoginEmailInput,
.vaiLoginPasswordInput {
  padding: 8px 15px 8px 5px;
  width: 390px;
  height: 33px;
  background: #fafafa;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  color: #6d6d73;
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  outline: none;
}

.vaiLoginEmailInput {
  margin-top: 30px;
}

.vaiLoginPasswordInput {
  margin-top: 20px;
}

.vaiSignInButton,
.vaiResetPasswordButton {
  background-color: #13ad83;
  width: 390px;
  height: 43px;
  border-radius: 4px;
  padding: 11px 24px;
  border: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 21px;
  margin-top: 40px;
}

.vaiForgotPasswordText,
.vaiBackText {
  color: #6d6d73;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.03em;
  margin-top: 15px;
}
